import { Routes } from '@angular/router';
import { createGuardedGridRoute } from '../../../../shared/utils';
import { clientsGuard, vendorAlertsGuard } from '../../guards';
import { vendorTypePermissionGuard } from '../../guards/vendor-type-permission.guard';
import { createContextSmartRedirectGuard } from '../../guards/smart-redirect.guard';
import { VENDOR_ADMIN_FEATURES } from './vendor-administration.config';
import { AdminGridContainerComponent } from '../admin-grid-container/admin-grid-container.component';
import { AdminTreetableContainerComponent } from '../admin-treetable-container/admin-treetable-container.component';
import { VendorContainerComponent } from './vendor-container/vendor-container.component';

export const ADMIN_VENDORS_ROUTES: Routes = [
  {
    path: '',
    component: VendorContainerComponent,
    children: [
      {
        path: '',
        redirectTo: 'details',
        pathMatch: 'full',
      },
      {
        path: 'accounts',
        children: [
          {
            path: '',
            canActivate: [
              createContextSmartRedirectGuard(
                'vendor',
                'accounts',
                ['vendor-users', 'vendor-user-roles'],
                'vendor',
                VENDOR_ADMIN_FEATURES,
              ),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'details',
        loadChildren: () =>
          import('./vendor-details/vendor-details.routes').then(
            (m) => m.ADMIN_VENDOR_DETAILS_ROUTES,
          ),
        data: { nestedContentScrollable: true },
      },
      {
        path: 'hardware',
        children: [
          {
            path: '',
            canActivate: [
              createContextSmartRedirectGuard(
                'vendor',
                'hardware',
                ['vendor-hardware-devices'],
                'vendor',
                VENDOR_ADMIN_FEATURES,
              ),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'organization',
        children: [
          {
            path: '',
            canActivate: [
              createContextSmartRedirectGuard(
                'vendor',
                'organization',
                ['vendor-cost-centres'],
                'vendor',
                VENDOR_ADMIN_FEATURES,
              ),
            ],
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type',
            canActivate: [vendorTypePermissionGuard],
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [vendorTypePermissionGuard],
            component: AdminTreetableContainerComponent,
          },
        ],
      },
      {
        path: 'profiles',
        children: [
          {
            path: '',
            canActivate: [
              createContextSmartRedirectGuard(
                'vendor',
                'profiles',
                [
                  'vendor-company-groups',
                  'vendor-io-types',
                  'vendor-device-config-profiles',
                  'vendor-overspeed-profiles',
                  'vendor-road-profiles',
                  'vendor-geo-lock-profiles',
                  'vendor-custom-fields',
                ],
                'vendor',
                VENDOR_ADMIN_FEATURES,
              ),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: 'tree',
            children: [
              {
                path: ':type',
                canActivate: [vendorTypePermissionGuard],
                component: AdminTreetableContainerComponent,
              },
              {
                path: ':type/:entityType/:entityId',
                canActivate: [vendorTypePermissionGuard],
                component: AdminTreetableContainerComponent,
              },
            ],
          },
          {
            path: 'grid',
            children: [
              {
                path: ':type',
                canActivate: [vendorTypePermissionGuard],
                component: AdminGridContainerComponent,
              },
              {
                path: ':type/:entityType/:entityId',
                canActivate: [vendorTypePermissionGuard],
                component: AdminGridContainerComponent,
              },
            ],
          },
        ],
      },
      {
        path: 'providers',
        children: [
          {
            path: '',
            canActivate: [
              createContextSmartRedirectGuard(
                'vendor',
                'providers',
                ['vendor-email-providers', 'vendor-sms-gateway-providers'],
                'vendor',
                VENDOR_ADMIN_FEATURES,
              ),
            ],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            canActivate: [vendorTypePermissionGuard],
            component: AdminGridContainerComponent,
          },
        ],
      },
      ...createGuardedGridRoute(
        'clients',
        clientsGuard,
        AdminGridContainerComponent,
        VENDOR_ADMIN_FEATURES['clients'].permissions,
        undefined,
        'grid/:type',
        'vendor',
      ),
      ...createGuardedGridRoute(
        'alerts',
        vendorAlertsGuard,
        AdminGridContainerComponent,
        undefined,
        undefined,
        'grid/:type',
        'vendor',
      ),
    ],
  },
];
