import { CanActivateFn, Router } from '@angular/router';
import { inject } from '@angular/core';
import { PermissionService } from '../../../core/services/permission.service';
import {
  VENDOR_ADMIN_FEATURES,
  VendorFeatureId,
} from '../components/vendors/vendor-administration.config';
import { isNullOrUndefined } from '../../../shared/utils';

/**
 * Guard for vendor admin routes using dynamic :type parameter.
 * Uses VENDOR_ADMIN_FEATURES to check permissions with vendor context.
 */
export const vendorTypePermissionGuard: CanActivateFn = async (route) => {
  const permissionService = inject(PermissionService);
  const router = inject(Router);
  const type = route.paramMap.get('type') as VendorFeatureId | null;

  if (isNullOrUndefined(type)) {
    return true;
  }

  if (!(type in VENDOR_ADMIN_FEATURES)) {
    return true;
  }

  const featureConfig = VENDOR_ADMIN_FEATURES[type];
  const permissions =
    'permissions' in featureConfig ? featureConfig.permissions : undefined;

  if (!permissionService.canAccess(permissions, 'vendor')) {
    await router.navigate(['/unauthorized'], {
      queryParams: {
        requiredPermission: JSON.stringify(permissions ?? 'no_permission'),
      },
    });
    return false;
  }

  return true;
};
